.formikWrapper {
  height: 100vh;

  background-color: var(--c-dcebf7);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.formikContainer {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form {
  width: 335px;

  background-color: var(--white);

  display: flex;
  flex-direction: column;

  padding: 40px 24px;
  margin-bottom: 18px;

  border-radius: 8px;

  @media (min-width: 768px) {
    width: 480px;

    padding: 40px;
    margin-bottom: 24px;
  }
}

.formTitle {
  color: var(--accent);

  font-weight: 600;
  font-size: 18px;
  line-height: 1.333;

  margin-bottom: 8px;

  text-shadow: 0px 9.399999618530273px 57.6875px 0px var(--shadow-1),
    0px 47px 355px 0px var(--shadow-2);

  @media (min-width: 768px) {
    font-size: 24px;
    line-height: 1;

    margin-bottom: 22px;
  }
}

.labelBox {
  display: flex;
  flex-direction: column;

  margin-top: 24px;

  @media (min-width: 768px) {
    margin-top: 18px;
  }
}

.label,
.labelError,
.labelOk {
  font-weight: 600;
  font-size: 12px;
  line-height: 1.167;

  margin-bottom: 8px;

  @media (min-width: 768px) {
    font-size: 14px;
  }
}

.label {
  color: var(--black);
}

.labelError {
  color: var(--c-e74a3b);
}

.labelOk {
  color: var(--c-3cbc81);
}

.inputBox {
  position: relative;
}

.input {
  position: relative;

  width: 100%;

  background-color: var(--white);

  font-weight: 600;
  font-size: 14px;
  line-height: 1.286;

  padding: 14px;

  border: 1px solid var(--c-rgba22022722906);
  border-radius: 8px;

  outline: none;

  @media (min-width: 768px) {
    font-size: 16px;
    line-height: 1.125;
  }

  &::placeholder {
    color: var(--c-616161);

    font-weight: 400;
    font-size: 14px;
    line-height: 1.286;

    @media (min-width: 768px) {
      font-size: 16px;
      line-height: 1.125;
    }
  }

  &:focus,
  &:hover {
    border: 1px solid var(--accent);
  }
}

.show {
  position: absolute;
  margin-top: 22px;
}

.showError {
  position: absolute;
  margin-top: 22px;
  margin-left: -25px;
}

.iconShow {
  position: absolute;
  top: 17px;
  left: 255px;

  width: 20px;
  height: 20px;

  cursor: pointer;

  @media (min-width: 768px) {
    left: 370px;
  }
}

.inputError,
.inputOk {
  width: 100%;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.286;

  padding: 14px;

  border-radius: 8px;

  outline: none;
}

.inputError {
  background-color: var(--white);

  border: 1px solid var(--c-e74a3b);
}

.inputOk {
  background-color: var(--white);

  border: 1px solid var(--c-3cbc81);
}

.messageError,
.messageOk {
  font-weight: 600;
  font-size: 12px;
  line-height: 1.333;

  margin-top: 8px;
  margin-left: 18px;
}

.messageError {
  color: var(--c-e74a3b);
}

.messageOk {
  color: var(--c-3cbc81);
}

.iconError,
.iconDone {
  position: absolute;
  top: 15px;
  left: 255px;

  width: 20px;
  height: 20px;

  @media (min-width: 768px) {
    left: 370px;
  }
}

.iconError {
  fill: var(--c-e74a3b);
}

.iconDone {
  fill: var(--c-3cbc81);
}

.formBtn {
  width: 100%;

  background-color: var(--accent);
  color: var(--white);

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  padding: 14px;
  margin-top: 32px;

  border: none;
  border-radius: 16px;

  box-shadow: 4px 2px 16px 0px var(--c-rgba136165191048);

  cursor: pointer;

  @media (min-width: 768px) {
    margin-top: 40px;
  }

  @media (min-width: 1440px) {
    margin-top: 48px;
  }

  &:hover,
  &:focus {
    transform: scale(1.02);
  }
}

.btnText {
  color: var(--white);

  font-weight: 600;
  font-size: 14px;
  line-height: 1.286;
  letter-spacing: -0.02em;

  @media (min-width: 768px) {
    font-size: 18px;
    line-height: 1.333;
    letter-spacing: -0.36px;
  }
}

.svg {
  width: 18px;
  height: 18px;

  fill: transparent;
  stroke: var(--white);

  @media (min-width: 768px) {
    width: 20px;
    height: 20px;
  }
}

.img {
  display: none;

  @media (min-width: 1440px) {
    position: fixed;
    right: 20px;
    bottom: 20px;

    width: 368px;
    height: 521px;

    display: block;
  }
}

.btnEmailBox {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  margin-top: 10px;
}

.btnEmailTitle,
.btnEmail {
  color: #616161;

  font-weight: 600;
  font-size: 12px;
  line-height: 1.333;
}

.btnEmail {
  padding: 5px;

  border: none;
  border-radius: 5px;

  box-shadow: 4px 2px 16px 0px var(--c-rgba136165191048);

  cursor: pointer;

  &:hover,
  &:focus {
    transform: scale(1.1);
  }

  &:active {
    transform: translateY(1px);
  }
}
